import React from "react"
import {getUser} from "../components/services/auth"

const Profile = () => (
    <React.Fragment>
        <h1>Your profile Lk</h1>
        <ul>
            <li>Name: {getUser().name}</li>
            <li>E-mail: {getUser().email}</li>
        </ul>
    </React.Fragment>
)

export default Profile
