import React from 'react';
import {StaticQuery, graphql, Link} from "gatsby"
import { NavItem } from "reactstrap";
import {API_URL} from "../services/helper";


const TopMenu = () => {
    return (
        <StaticQuery query={
            graphql`
            {
                sporttalk{
                    leagues{
                        id
                        name
                        club{
                            name
                        }
                        logo{
                            url
                        }
                    }
                }
            }
            `
        } render={data =>
            <React.Fragment>
                <nav style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center"
                }} className="header-nav ">
                    <ul style={{
                        display: "flex",
                        alignItems:"center"
                    }} className="nav-list">
                        {data.sporttalk.leagues.map((league, key)=>
                            <NavItem key={key}>
                                <Link activeClassName={"nav-active"} className={"nav-link"} to={'/league/'+ league.name} >
                                    <span className={"icon"}>
                                        <img src={API_URL + league.logo.url} alt="logo item" />
                                    </span>
                                    <span className={"text"}>{league.name}</span>
                                </Link>
                            </NavItem>
                        )}
                    </ul>
                </nav>
            </React.Fragment>
        } />
    );
};

export default TopMenu;
