import React from "react"
import { Link, navigate } from "gatsby"
import { Form } from "reactstrap";

import { getUser, isLoggedIn, logout } from "../services/auth"

export default () => {
    const content = { message: "", login: true }
    if (isLoggedIn()) {
        content.message = `Hello, ${getUser().name}`
    } else {
        content.message = "You are not logged in"
    }
    return (
        <React.Fragment>
            {/*<span>{content.message}</span>*/}
            <div className="header-top-right"
            style={{
                display: "flex",
                alignItems: "center",
            }}
            >
                <div className="search d-none d-md-block">
                    <Form >
                        <input type="search" className="header-search" placeholder="Search" />
                        <input type="submit" className="search-icon" value="" />
                    </Form>
                </div>
                <div className="user-login d-none d-md-block">
                    {isLoggedIn() ? (
                            <React.Fragment>
                                <Link to="/user/profile">Profile</Link>
                                <a
                                    href="/"
                                    onClick={event => {
                                        event.preventDefault()
                                        logout(() => navigate(`/`))
                                    }}
                                >
                                    Logout
                                </a>
                            </React.Fragment>
                        ) :
                        <React.Fragment>
                            <Link activeClassName={"nav-active"} className="user-login-item sign-in" to="/user/login">Sign in</Link>
                            <Link activeClassName={"nav-active"} className="user-login-item register" to="/user/login">Register</Link>
                        </React.Fragment>
                    }

                </div>
            </div>
        </React.Fragment>
    )
}
