import React from 'react';
import { Link, StaticQuery, graphql} from "gatsby";
import {Nav, NavItem, Row, Container} from "reactstrap";
import Col from "reactstrap/es/Col";

import Facebook from "../../images/facebook-logo.svg";
import Twitter from "../../images/twitter-logo.svg";
import Google from "../../images/google-logo.svg";
import Instagram from "../../images/instagram-logo.svg";
import Youtube from "../../images/youtube-logo.svg";
import "./index.scss"


function Footer() {
    return (
                <StaticQuery query={
                    graphql`
                {
                sporttalk{
                    pages{
                        id
                        title
                        in_footer
                      }
                    }
                }
                `
                } render={data =>
                    <React.Fragment>
                        <footer className="footer">
                            <Container>
                                <div className="footer-top">
                                    <Row>
                                        <Col md={6}>
                                            <div className="logo">
                                                <Link to={"/"} className="logo-item">
                                                    Sports
                                                    <span className="color-red">Talk</span>
                                                </Link>
                                            </div>
                                            <div className="footer-nav">
                                                <Nav className="list">
                                                        {data.sporttalk.pages.map((page, key)=>
                                                            (page.in_footer === true)?
                                                            <NavItem key={key} className={"list-item"}>
                                                                <Link partiallyActive={true}
                                                                      activeClassName={"nav-active"}
                                                                      className={"nav-link"}
                                                                      to={'/' + page.title.toLowerCase().replace(' ','_')}
                                                                      style={{padding: 0}}
                                                                >{page.title}</Link>
                                                            </NavItem> : ''
                                                        )}
                                                </Nav>
                                            </div>
                                        </Col>
                                        <Col md={6}>
                                            <div className="title title__small title__footer">
                                                <span className="rectangle rectangle__red"/>Follow us
                                            </div>
                                            <div className="footer-nav footer-nav__social">
                                                <Nav className="list">
                                                    <NavItem className="list-item">
                                                        <Link to={"/"} className="list-link">
                                                            <img src={Facebook} alt="facebook" />
                                                        </Link>
                                                    </NavItem>
                                                    <NavItem className="list-item">
                                                        <Link to={"/"} className="list-link">
                                                            <img src={Twitter} alt="twitter" />
                                                        </Link>
                                                    </NavItem>
                                                    <NavItem className="list-item">
                                                        <Link to={"/"} className="list-link">
                                                            <img src={Google} alt="google" />
                                                        </Link>
                                                    </NavItem>
                                                    <NavItem className="list-item">
                                                        <Link to={"/"} className="list-link">
                                                            <img src={Instagram} alt="instagram" />
                                                        </Link>
                                                    </NavItem>
                                                    <NavItem className="list-item">
                                                        <Link to={"/"} className="list-link">
                                                            <img src={Youtube} alt="youtube" />
                                                        </Link>
                                                    </NavItem>
                                                </Nav>
                                            </div>
                                        </Col>
                                    </Row>
                                </div>
                                <div className="footer-bottom">
                                    <Row>
                                        <Col md={12}>
                                            <div className="service-policy">
                                                {/*{data.sporttalk.pages.map((page, key)=>*/}
                                                {/*    (page.in_footer !== true)?*/}
                                                {/*        <Link key={key}*/}
                                                {/*              partiallyActive={true}*/}
                                                {/*              className={"nav-link"}*/}
                                                {/*              to={'/' + page.title.toLowerCase().replace(' ','_')}*/}
                                                {/*        >{page.title}</Link> : ''*/}
                                                {/*)}*/}
                                                <Link to={"/terms_of%20service"} className="link service">
                                                    Terms of Service
                                                </Link>
                                                <Link to={"/privacy_policy"} className="link policy">
                                                    Privacy Policy
                                                </Link>
                                            </div>
                                            <div className="copyright">
                                                Copyright  2018 Lorem Ipsum, Inc. abc System, Inc. All Rights Reserved.
                                            </div>
                                        </Col>
                                    </Row>
                                </div>
                            </Container>
                        </footer>
                    </React.Fragment>
                }/>
    );
}

export default Footer;
